import { Button, TextField, Typography } from "@material-ui/core";
import { Component, default as React } from "react";
import { connect } from "react-redux";
import { postData } from "../../actions/action";
import { ADD_GROCERY_URL, createUrl } from "../../constants/url.constants";
import { isAccessible } from "../../helpers/visibility.helper";
import GroceryModel from "../../models/GroceryModel";
import PermissionDenied from "../common/PermissionDenied";

class Grocery extends Component {
  constructor(props) {
    super(props);
    this.state = { grocery: "" };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange($event) {
    this.setState({ grocery: $event.target.value });
  }

  sendGrocery = () => {
    const { grocery } = this.state;
    const { profileObj, sendGrocery } = this.props;
    if (grocery && grocery.length > 3) {
      let groceryObj = new GroceryModel(profileObj, grocery, new Date());
      sendGrocery(groceryObj);
      this.setState({ grocery: "" });
    }
  };
  render() {
    const { profileObj } = this.props;
    const { grocery } = this.state;

    return isAccessible(profileObj, "grocery") ? (
      <div>
        <Typography paragraph>Grocery</Typography>
        <TextField
          id="standard-multiline-static"
          label="Item Name"
          multiline
          rows="6"
          placeholder="Add something to the Grocery list"
          style={{ width: "100%" }}
          margin="normal"
          onChange={this.handleChange}
          value={grocery}
        />
        <Button variant="contained" color="primary" onClick={this.sendGrocery}>
          Send
          <i
            style={{ marginLeft: "10px" }}
            className="fa fa-paper-plane"
            aria-hidden="true"
          ></i>
        </Button>
      </div>
    ) : (
      <PermissionDenied profileObj={profileObj} />
    );
  }
}

Grocery.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    sendGrocery: groceryObj =>
      dispatch(postData(createUrl(ADD_GROCERY_URL), groceryObj))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Grocery);
